<template>
  <form class="pure-form pure-form-aligned pd-5 bd-1 shadow-2">
    <div class="pure-control-group flex-row form-group">
      <label for="keyword">搜索</label>
      <select name="target" id="target" :value="filter.target">
        <option value="">任何</option>
        <option value="title">标题</option>
        <option value="description">描述</option>
      </select>
      <input class="ml-1" type="text" name="keyword" id="keyword" :value="filter.keyword">
    </div>
    <div class="pure-control-group form-group">
      <label for="status">状态</label>
      <select name="status" id="status" :value="filter.status">
        <option value="">任何</option>
        <option value="0">接受</option>
        <option value="1">未接受</option>
      </select>
    </div>
    <div class="pure-control-group">
      <label for=""></label>
      <button class="pure-button pure-button-primary" @click.prevent="toSearch">搜索</button>
      <button class="ml-1 pure-button button-warning" @click.prevent="toReset">重置</button>
    </div>
  </form>
</template>

<script>
export default {
  data() {
    return {
      filter: {
        keyword: '',
        target: '',
        status: '',
      }
    }
  },
  methods: {
    toSearch() {
      const keyword = document.getElementById('keyword').value;
      const target = document.getElementById('target').value;
      const status = document.getElementById('status').value;
      const filter = {
        keyword,
        target,
        status,
      }
      this.$emit('search', filter);
    },
    toReset() {
      document.getElementById('keyword').value = '';
      document.getElementById('target').value = '';
      document.getElementById('status').value = '';
      this.$emit('reset');
    }
  }
}
</script>

<style lang="scss" scoped>
.form-group {
  display: flex;
  flex-direction: row;
  label {
    align-self: center;
  }
  select {
    width: 10%;
  }
  input {
    width: 60%;
  }
}
</style>
