<template>
  <div class="pd-1 flex-column w-80 ml-auto mr-auto" v-loading="loading">
    <button 
    class="pure-button pure-button-primary flex-item-end" 
    @click="toAddRequest"
    >
    发起请求
    </button>
    <request-filter
    class="mt-1"
    @search="toSearch"
    @reset="toReset"
    />
    <request-list  
    class="flex-1 mt-1 relative"
    :list="list"
    :loading="loading"
    />
    <pager 
    class="mt-auto"
    :len="pager.len"
    :total="pager.total"
    :active="pager.page"
    @click="toPage"
    />
  </div>
</template>

<script>
import RequestFilter from './components/request-filter.vue';
import RequestList from './components/request-list.vue';

export default {
  components: {
    RequestFilter,
    RequestList,
  },
  data() {
    return {
      loading: true,

      list: [],

      pager: {
        page: 1,
        len: 10,
        total: 0,
      },

      filter: {
        status: '',
        keyword: '',
        target: '',
      }
    }
  },
  mounted() {
    this.getData();
  },
  methods: {
    getData() {
      const query = `keyword=${this.filter.keyword}&status=${this.filter.status}&target=${this.filter.target}&page=${this.pager.page}&limit=${this.pager.len}`;
      this.$axios.get(`${this.$url.requests}?${query}`)
      .then((resp) => {
        this.list = resp.data.results;
        this.pager.total = resp.data.total;
        this.loading = false;
      })
    },
    toSearch(filter) {
      this.filter = filter;
      this.getData();
    },
    toReset() {
      this.filter = {
        target: '',
        keyword: '',
        status: '',
      }
      this.getData();
    },
    toAddRequest() {
      if (!window.$user) {
        this.$alert('error', '请先登录');
        return;
      }
      this.$router.push('/add/request');
    },
    toPage(index) {
      this.pager.page = index;
      this.getData();
    }
  }
}
</script>