<template>
  <no-data v-if="!loading && list.length === 0">
  </no-data>
  <table v-else class="w-100 table-fixed pure-table pure-table-bordered">
    <thead>
      <tr>
        <th class="w-50">标题</th>
        <th>时间</th>
        <th>用户名</th>
        <th>VOTE</th>
        <th>评论</th>
        <th>状态</th>
      </tr>
    </thead>
    <tbody>
      <tr
      v-for="item in list"
      :key="item.title"
      >
      <td class="w-50">
        <router-link :to="'/requests/' + item.title">
          {{ item.title }}
        </router-link>
      </td>
      <td class="w-10">{{ item.createdAt | clock2 }}</td>
      <td class="w-10">{{ item.username }}</td>
      <td class="w-10">{{ item.votes }}</td>
      <td class="w-10">{{ item.comments.length }}</td>
      <td class="w-10">
        {{ item.status === 0 ? 'Approved' : 'Unapproved'}}
      </td>
      </tr>
    </tbody>
  </table>
</template>

<script>
export default {
  props: {
    list: {
      type: Array,
      required: true,
    },
    loading: {
      type: Boolean,
      required: true,
    }
  },
}
</script>