<template>
  <div class="flex-column">
    <ul class="flex-1" v-if="approved">
      <li
      v-for="(c, index) in comments"
      :key="c._id"
      >
        <div class="reply">
          <div class="reply-left">
            <img class="reply-avatar" :src="c.avatar" />
            <i v-if="index === approvedIndex" class="fas fa-check reply-flag reply-flag--checked">
            </i>
          </div>
          <div class=" reply-right">
            <div class="reply-meta">
              <span class="reply-meta__username">
                {{ c.username }}
              </span>
              <span class="ml-1 reply-meta__createdAt">
                {{ c.createdAt | formatDateTime }}
              </span>
            </div>
            <div class="reply-content">{{ c.content }}</div>
          </div>
        </div>
      </li>
    </ul>
    <ul class="flex-1" v-else>
      <li
      v-for="(c, index) in comments"
      :key="c._id"
      >
        <div class="reply">
          <div class="reply-left">
            <img class="reply-avatar" :src="c.avatar" />
            <i @click="toApprove(index)" class="fas fa-check reply-flag">
            </i>
          </div>
          <div class="reply-right">
            <div class="reply-meta">
              <span class="reply-meta__username">
                {{ c.username }}
              </span>
              <span class="ml-1 reply-meta__createdAt">
                {{ c.createdAt | formatDateTime }}
              </span>
            </div>
            <div class="reply-content">{{ c.content }}</div>
          </div>
        </div>
      </li>
    </ul>
    <form class="mt-5 post-reply-form pure-form pure-form-stacked" @submit.prevent="toComment">
      <textarea 
      class="w-100" name="request-comment-input" id="request-comment-input" cols="30" rows="10" required
      :disabled="flag"
      ></textarea>
      <button class="pure-button" :disabled="flag">Post</button>
    </form>
  </div>
</template>

<script>
export default {
  props: {
    comments: {
      type: Array,
      required: true,
    },
    approved: {
      type: Boolean,
      required: true,
    },
    approvedIndex: {
      type: Number,
    },
    flag: {
      type: Boolean,
      default: false,
    }
  },
  methods: {
    toApprove(index) {
      this.$emit('approve', index);
    },
    toComment() {
      if (this.flag) {
        return;
      }
      const el = document.getElementById('request-comment-input');
      if (el && el.value.trim()) {
        this.$emit('comment', el.value.trim());
        el.value = '';
      }
    }
  }
}
</script>

<style lang="scss" scoped>
.reply {
  display: flex;
  flex-direction: row;
  padding: $pd-1;
  border-bottom: 1px solid #ccc;
  min-height: 100px;

  .reply-left {
    display: flex;
    flex-direction: column;
    .reply-flag {
      font-size: 50px;
      color: #777;
      &:hover {
        color: $success;
        cursor: pointer;
      }
    }
    .reply-flag--checked {
      color: $success;
      &:hover {
        cursor: none;
      }
    }
  }

  .reply-avatar {
    width: 50px;
    height: 50px;
    border-radius: 50px;
    object-fit: cover;
  }

  .reply-right {
    flex: 1;
    display: flex;
    flex-direction: column;
    margin-left: 0.4em;
    .reply-meta {
      color: $font-light;
      font-size: $font-sm;
    }
    .reply-content {
      white-space: pre-line;
    }
  }
}
</style>
