<template>
  <div class="request pd-1 flex-column">
    <div class="request-header">
      <h1>
        {{ request.title }}
      </h1>
      <p>
        {{ request.description }}
      </p>
    </div>
    <request-comments 
    class="flex-1"
    :comments="request.comments"
    :approved="request.status === REQUEST_APPROVED"
    :approved-index="request.approvedIndex"
    :flag="comment.flag"
    @approve="toApprove"
    @comment="toComment"
    />
  </div>
</template>

<script>
import RequestComments from './components/request-comments';

export default {
  components: {
    RequestComments,
  },
  props: {
    title: {
      type: String,
      required: true,
    }
  },
  data() {
    return {
      request: {
        title: '',
        description: '',
        comments: [],
      },
      loading: true,

      comment: {
        flag: false,
      }
    }
  },
  mounted() {
    this.getData();
  },
  methods: {
    getData() {
      this.$axios.get(`${this.$url.requests}/${this.title}`)
      .then((resp) => {
        this.request = resp.data;
        this.loading = false;
      })
    },
    toComment(content) {
      if (this.comment.flag) {
        return;
      }
      const user = window.$user;
      const comment = {
        title: this.title,
        username: user.username,
        avatar: user.avatar,
        content: content,
      }
      this.comment.flag = true;
      this.$axios.post(`${this.$url.requests}/${this.title}/comments`, comment)
      .then((resp) => {
        this.request.comments.push(resp.data);
        this.comment.flag = false;
      })
      .catch(() => {
        this.comment.flag = false;
      })
    },
    toApprove(index) {
      this.request.status = 0;
      this.request.approvedIndex = index;
      const payload = {
        status: this.request.status,
        approvedIndex: this.request.approvedIndex,
      }
      this.$axios.put(`${this.$url.requests}/${this.title}/status`, payload)
    }
  }
}
</script>

<style lang="scss" scoped>
.request {
  width: 80%;
  margin: 0 auto;
}
.request-header {
  border: 1px solid silver;
  margin: 2em 0;
  padding: 0.4em 0.6em;
}
</style>
